
import { ref, toRefs } from 'vue';
import { debounce } from 'lodash';
import { ElMessage } from 'element-plus';
import useClipboard from 'vue-clipboard3';
import { useLikeMutation } from '@/graphql';
import { DEVICE_INFO, DOWNLOAD_URL, HOST_NAME, TX_ENV } from '@/config';

export default {
  props: {
    post: Object
  },
  setup(props) {
    const { post } = toRefs(props);
    const liked = ref(post.value?.liked || false);
    const count_likes = ref(post.value?.count_likes || 0);
    const videoLink = ref(
      post.value?.id ? `#${post.value?.description || ''}# https://${HOST_NAME}/video/${post.value?.id} 点击观看！` : ''
    );

    const { toggleLike, onDone, onError } = useLikeMutation(
      { id: post.value?.id, type: 'POST' },
      { fetchPolicy: 'network-only' }
    );

    onDone(({ data }) => {
      if (data) {
        liked.value = data?.toggleLike?.isLiked;
      }
    });
    onError(() => {
      liked.value ? count_likes.value-- : count_likes.value++;
    });

    const favoriteHandler = debounce(() => {
      liked.value ? count_likes.value-- : count_likes.value++;
      liked.value = !liked.value;
      toggleLike();
    }, 500);

    const { toClipboard } = useClipboard();
    async function shareVideoLink() {
      if (videoLink.value) {
        try {
          await toClipboard(videoLink.value);
          ElMessage({ message: '复制成功', type: 'success' });
        } catch (error) {
          ElMessage({ message: '复制失败', type: 'error' });
        }
      } else {
        ElMessage({ message: '链接出错', type: 'error' });
      }
    }

    function watchTogether() {
      ElMessage({
        message: '即将上线，敬请期待',
        type: 'info'
      });
    }

    return {
      liked,
      count_likes,
      favoriteHandler,
      videoLink,
      shareVideoLink,
      watchTogether
    };
  },
  methods: {
    showReportModal() {
      this.$bus.emit('SHOW_REPORT_MODAL', { id: this.post.id, title: '举报视频', type: 'posts' });
    },
    downloadApp() {
      if (TX_ENV) {
        this.$bus.emit('SHOW_BROWSER_OPEN_PROMPT');
        window.pwaEventTrack('用APP看', '腾讯浏览器');
      } else if (DEVICE_INFO.OS === 'IOS') {
        this.$bus.emit('SHOW_IOS_PWA_PROMPT');
        window.pwaEventTrack('用APP看', 'iOS浏览器');
      } else {
        window.open(DOWNLOAD_URL, '_blank');
        window.pwaEventTrack('用APP看', '安卓浏览器');
      }
    }
  }
};
